import React, { Component } from 'react'
// connect方法的作用：将额外的props传递给组件，并返回新的组件，组件在该过程中不会受到影响
import { connect } from 'react-redux'
// 引入action
// import { setPageTitle } from '../../store/actions.js'


import HeaderNav from "../../components/com_home/headerNav"
import CatBigWords from "../../components/com_home/catBigWords"
import SollTab from "../../components/com_home/sollTab"

class Home extends Component{
    
    render(){
        return(
            < >
                <div><HeaderNav/></div>
                <div><CatBigWords/></div>
                <div><SollTab/></div>
            </>
        )
    }
}

export default connect()(Home)