import React, { Component } from 'react'
// connect方法的作用：将额外的props传递给组件，并返回新的组件，组件在该过程中不会受到影响
import { connect } from 'react-redux'
// 引入action

class HeaderNav extends Component {
    constructor() {
        super()
    }
    render() {
        return (
            <div className="headerbox" style={styles.header}>

                <h1 style={styles.inline_block} >徐大仙</h1>
                <div style={styles.inline_block}>
                    <a style={styles.a} href="https://www.jianshu.com/u/e974af4433d8" target="_blanket">
                        简书
                    </a>
                    
                    <a style={styles.a} href="https://github.com/ngxu" target="_blanket">
                        Github
                    </a>
                    <a style={styles.a} href="" target="_blanket">
                        简历
                    </a>
                    
                    <a style={styles.a} href="" target="_blanket">
                        关于
                        </a>

                    <a style={styles.a} href="https://segmentfault.com/u/user_evubh9cs" target="_blanket">
                        SegmentFault
                    </a>

                    <a style={styles.a} href="./blog/" target="_blanket">
                        博客
                    </a>
                </div>

            </div>
        )
    }
}



const styles = {
    header: {
        alignItems: "center",
        display: "flex",
        left: 0,
        right: 0,
        position: "fixed",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: "0 2rem",
        top: 0,
        height: "80px",
    },
    inline_block: {
        display: "inline-block"
    },
    name: {

    },
    a: {
        cursor: "pointer",
        padding: "0 2.5rem",
        color: "inherit",
        textDecoration: "none",
        fontWeight: "500",
        lineHeight: "80px",
    }
}




export default connect()(HeaderNav)