
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';


import Home from '../src/page/home/index'
// Provider是react-redux两个核心工具之一，作用：将store传递到每个项目中的组件中
// 第二个工具是connect，稍后会作介绍
import { Provider } from 'react-redux'
// 引入创建好的store实例
import store from './store/index.js'
ReactDOM.render(


  (
    <div>
      {/* 将store作为prop传入，即可使应用中的所有组件使用store */}

      <Provider store={store}>
        {
          (() => {
            if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
              //alert("当前是手机端")
              return <div style={{textAlign:'center',paddingTop:20}}>请用电脑打开，移动端还没做，没时间~</div>//如果是手机打开加载手机界面
            } else {
              //alert("当前是电脑端")
              return <div className="box"><Home /></div>//如果是电脑打开加载电脑界面
            }

          })()

        }

      </Provider>
    </div>
  ),
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
